import React from 'react';
import PropTypes from 'prop-types';

/**
 * OptimisticUpdateWrapper - Wraps components to show optimistic update states
 * 
 * @param {boolean} isOptimistic - Whether the item is in optimistic state
 * @param {string} status - Status: 'pending', 'success', 'error'
 * @param {ReactNode} children - Content to wrap
 * @param {string} className - Additional CSS classes
 */
const OptimisticUpdateWrapper = ({
    isOptimistic = false,
    status = 'pending',
    children,
    className = ""
}) => {
    if (!isOptimistic) {
        return <>{children}</>;
    }

    const statusClass = `optimistic-update optimistic-update-${status}`;
    const allClasses = `${className} ${statusClass}`.trim();

    return (
        <div className={allClasses}>
            {children}
        </div>
    );
};

OptimisticUpdateWrapper.propTypes = {
    isOptimistic: PropTypes.bool,
    status: PropTypes.oneOf(['pending', 'success', 'error']),
    children: PropTypes.node.isRequired,
    className: PropTypes.string,
};

export default OptimisticUpdateWrapper;

