import React from 'react';
import PropTypes from 'prop-types';

/**
 * LoadingButton - A reusable button component with loading state
 * 
 * @param {boolean} isLoading - Whether the button is in loading state
 * @param {string} loadingText - Text to show when loading (default: "Loading...")
 * @param {string} className - Additional CSS classes
 * @param {string} variant - Button variant: 'primary', 'secondary', 'danger', 'default'
 * @param {boolean} disabled - Whether button is disabled
 * @param {function} onClick - Click handler
 * @param {ReactNode} children - Button content
 * @param {string} size - Button size: 'sm', 'md', 'lg'
 */
const LoadingButton = ({
    isLoading = false,
    loadingText = "Loading...",
    className = "",
    variant = "primary",
    disabled = false,
    onClick,
    children,
    size = "sm",
    type = "button",
    icon,
    ...props
}) => {
    const baseClasses = `btn btn-${variant} btn-${size}`;
    const allClasses = `${baseClasses} ${className} ${isLoading ? 'btn-loading' : ''}`.trim();

    return (
        <button
            type={type}
            className={allClasses}
            disabled={disabled || isLoading}
            onClick={onClick}
            {...props}
        >
            {isLoading && (
                <>
                    <span className="spinner-border spinner-border-sm mr-2" role="status" aria-hidden="true"></span>
                    <span className="sr-only">Loading...</span>
                </>
            )}
            {!isLoading && icon && <i className={`${icon} mr-1`}></i>}
            {isLoading ? loadingText : children}
        </button>
    );
};

LoadingButton.propTypes = {
    isLoading: PropTypes.bool,
    loadingText: PropTypes.string,
    className: PropTypes.string,
    variant: PropTypes.oneOf(['primary', 'secondary', 'danger', 'default', 'success', 'warning', 'info']),
    disabled: PropTypes.bool,
    onClick: PropTypes.func,
    children: PropTypes.node.isRequired,
    size: PropTypes.oneOf(['sm', 'md', 'lg']),
    type: PropTypes.oneOf(['button', 'submit', 'reset']),
    icon: PropTypes.string,
};

export default LoadingButton;

